<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="password" v-model="codedata.password" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.originalpassword')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="password" v-model="codedata.newpassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.newpassword')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="password" v-model="codedata.repassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.Repeatpassword')"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <yd-flexbox class="reda">
            <div class="chk">
              <yd-checkbox-group v-model="codedata.check" color="#278eed" size="18">
                  <yd-checkbox val="1" shape="circle"></yd-checkbox>
              </yd-checkbox-group>
            </div>
            <yd-flexbox-item>{{$t('data.resetspassword')}} <span @click="goimportcode()">{{$t('data.Immediatelyimport')}}</span></yd-flexbox-item>
        </yd-flexbox>
        <button type="button" class="btn_c" :class="{'btn_cbj':codedata.newpassword && codedata.password && codedata.repassword}" @click="save()">{{$t('code.ok')}}</button>
    </div>
  
</template>

<script>
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      codedata:{
        newpassword:'',
        password:'',
        repassword:'',
        check:[1]
      },
      headerNavData:{
         left:true,
         title:this.$t('code.password'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      }
    }
  },
  created(){
    console.log(eval(getCookie("codelist")))
  },
  methods:{
    //跳转导入钱包
    goimportcode(){
        this.$router.push({path:'/importcodes'})
    },
    save(){
      var that = this
      var password = this.codedata.password
      var newpassword = this.codedata.newpassword
      var repassword = this.codedata.repassword
      var publickey = this.$route.query.publickey
      if (password == "") {this.Toast(this.$t('data.originalpassword')); return}
      if (newpassword == "") {this.Toast(this.$t('data.newpassword')); return}
      if (repassword == "") {this.Toast(this.$t('data.Repeatpassword')); return}
      if (newpassword != repassword) {this.Toast(this.$t('data.Inconsistentpassword')); return}
      var data = JSON.parse(getCookie("codelist"))
      for (var i = 0; i < data.length; i++) {
        if (publickey == data[i].publickey) {
          if (password != data[i].password) {
            this.Toast("旧密码不正确"); return
          }else{
            data[i].password = newpassword
          }
        }
      }
      console.log(data)
      var codelist = JSON.stringify(data);
      setCookie("codelist",codelist)
      setTimeout(function(){
        that.$router.push({path:'/'})
        that.$router.go(-1)
      },300)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
}
.yd-checkbox-text{
  display: none;
}
.reda{
  padding: 0 0 0 .28rem;
  font-size: .26rem;
  color: #666;
}
.reda span{
  color: #278eed
}
.btn_c{
  margin: .35rem .28rem .15rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: calc(100% - .56rem);
  border: none;
  background: #cdcdcd;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_d{
  margin: 0 .28rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: calc(100% - .56rem);
  border: none;
  font-size: .28rem;
  color: #278eed;
  border-radius: 5px;
}
.btn_cbj{
  background: #278eed!important;
}
.createf{
  background: #f4f4f4;
  padding: .2rem .35rem; 
  font-size: .26rem;
  color: #278eed
}
</style>



